import { scroller } from "react-scroll";
import { withRouter, Link } from "react-router-dom";
import { NavHashLink } from "react-router-hash-link";

const scrollOpt = {
    isDynamic: true,
    offset: -80,
    spy: true,
    smooth: true,
    duration: 500,
};

class Menu extends React.Component {
    renderMenuItem = (item, i, active) => (
        <NavHashLink
            key={i}
            to={`/#${item.alias}`}
            className={`menu__item ${active && "menu__item_active"}`}
            scroll={(el) => {
                const elName = el.getAttribute("name");
                scroller.scrollTo(elName, scrollOpt);
            }}
        >
            {item.title}
        </NavHashLink>
    )

    renderLink = item => (
        <Link
            key={item.link}
            to={item.link}
            className="menu__item"
        >
            {item.title}
        </Link>
    )

    render() {
        const { menuItems, location, } = this.props;
        if (!menuItems.length) return null;
        return (
            <nav className="menu">
                {menuItems.map((item, i) => {
                    const active = location.hash === `#${item.alias}`;
                    return (
                        item.link
                            ? this.renderLink(item, i)
                            : this.renderMenuItem(item, i, active)
                    );
                })}
            </nav>
        );
    }
}
Menu.propTypes = {
    menuItems: PropTypes.instanceOf(Array),
    location: PropTypes.shape({ hash: PropTypes.string.isRequired, }).isRequired,
};
Menu.defaultProps = { menuItems: [], };
export default withRouter(Menu);
